import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "../style.css";
import Item from "./child/Item";
import Create from "./child/Create";
import Edit from "./child/Edit";
import Delete from "./child/Delete";
import CreateQuiz from "./child/CreateQuiz";
import EditQuiz from "./child/EditQuiz";
import DeleteQuiz from "./child/DeleteQuiz";
import Docs from "./child/Docs";

import ModalUID from "../../../components/ModalUID";
export default function Course() {
  const [state, setState] = useState({
    isOpenLesson: false,
    modal: {
      componentAction: null,
      title: "",
      method: null,
    },
  });

  const { modal, isOpenLesson } = state;

  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  function handleDelete() {
    setState({
      ...state,
      modal: {
        componentAction: <Delete></Delete>,
        title: "Xóa bài học",
        method: "REMOVE",
      },
    });
  }
  function showListDoc() {
    setState({
      ...state,
      modal: {
        componentAction: <Docs></Docs>,
        title: "Danh sách tài liệu",
        method: null,
      },
    });
  }
  function handleUpdate() {
    console.log("ssss");
    setState({
      ...state,
      modal: {
        componentAction: <Edit></Edit>,
        title: "Sửa bài học",
        method: "EDIT",
      },
    });
  }
  function handleDeleteQuiz() {
    setState({
      ...state,
      modal: {
        componentAction: <DeleteQuiz></DeleteQuiz>,
        title: "Xóa bài test",
        method: "REMOVE",
      },
    });
  }

  function handleUpdateQuiz() {
    console.log("ssss");

    setState({
      ...state,
      modal: {
        componentAction: <EditQuiz></EditQuiz>,
        title: "Sửa bài test",
        method: "EDIT",
      },
    });
  }
  function handleCreateQuiz() {
    setState({
      ...state,
      modal: {
        componentAction: <CreateQuiz></CreateQuiz>,
        title: "Tạo bài test",
        method: "EDIT",
      },
    });
  }

  console.log(state);
  return (
    <div className="" style={{}}>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
      <form>
              <div className="input-group mb-6" style={{ padding: "0px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm theo bài học"
                  style={{ maxWidth: "400px", minWidth: "300px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
        <div style={{ display: "flex" }}>
          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-danger btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-export"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Export Excel
            </span>
          </button>
          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-primary btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-import"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Import Excel
            </span>
          </button>
          <button
            className="btn btn-info btn-icon-split btn-sm show"
            onClick={() =>
              setState({
                ...state,
                modal: {
                  componentAction: <Create></Create>,
                  title: "Thêm bài học",
                  method: "CREATE",
                },
              })
            }
            data-toggle="modal"
            data-target="#modalUID"
          >
            <span className="icon text-white-50">
              <i className="fas fa-plus" />
            </span>
            <span className="text">Thêm bài học</span>
          </button>
        </div>
      </div>

      <div className="card shadow ">
     
        <div className="card-body">
          <div className="table-responsive">
            <table
              className="table table-border "
              id="dataTable"
              width="100%"
              cellSpacing={0}
            >
              <thead>
                <tr>
                  <th>STT</th>
                  <th>Tên bài học </th>
                  <th>Tên bài học</th>
                  <th>Người phụ trách</th>
                  <th>Tình trạng hoàn thành</th>

                  <th>Hành động</th>
                </tr>
              </thead>
              <tbody>
                <Item
                  handleDelete={handleDelete}
                  showListDoc={showListDoc}
                  handleUpdate={handleUpdate}
                  handleDeleteQuiz={handleDeleteQuiz}
                  handleUpdateQuiz={handleUpdateQuiz}
                  handleCreateQuiz={handleCreateQuiz}
                ></Item>
                <Item
                  handleDelete={handleDelete}
                  showListDoc={showListDoc}
                  handleUpdate={handleUpdate}
                  handleDeleteQuiz={handleDeleteQuiz}
                  handleUpdateQuiz={handleUpdateQuiz}
                  handleCreateQuiz={handleCreateQuiz}
                ></Item>
              </tbody>
            </table>
          </div>
          <nav
            aria-label="Page navigation"
            className="float-pagination undefined IKITECH"
          >
            <ul className="pagination  tab-pagination pg-blue">
              <li className="page-item null ">
                <a className="page-link">Trước</a>
              </li>
              <li className="page-item active ">
                <a className="page-link">1</a>
              </li>
              <li className="page-item null ">
                <a className="page-link">Sau</a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
      <ModalUID method={modal.method} title={modal.title}>
        {" "}
        {modal.componentAction}{" "}
      </ModalUID>
    </div>
  );
}
